<template>
  <div class="resto">
    <div v-if="loading">
      <v-progress-circular indeterminate color="purple"></v-progress-circular>
    </div>
    <div v-else>
      <CardRestoDetail :resto="item" />

      <form id="form" v-on:submit.prevent="createEmployee">
        <input type="text" v-model="newUser.name" placeholder="Username" />
        <input
          type="email"
          v-model="newUser.email"
          placeholder="email@email.com"
        />
        <input type="submit" value="Add User" />
      </form>
      <ul class="errors">
        <li v-show="!validation.name">Name cannot be empty.</li>
        <li v-show="!validation.email">
          Please provide a valid email address.
        </li>
      </ul>
    </div>
  </div>
</template>




<script>
import CardRestoDetail from "@/components/CardResto.vue";

import firebase from "@/firebaseInit";
const db = firebase.database();
export default {
  name: "Resto",
  data: function data() {
    return {
      loading: true,
      item: null,

      newUser: {
        name: "",
        email: "",
        date: "",
        heure: "",
        message: "",
      },
    };
  },
  components: {
    CardRestoDetail,
  },
  created() {
    this.searchTimeOut();
  },
  computed: {
    validation: function () {
      return {
        name: !!this.newUser.name.trim(),
      };
    },
    isValid: function () {
      var validation = this.validation;
      return Object.keys(validation).every(function (key) {
        return validation[key];
      });
    },
  },
  methods: {
    addUser: function () {
      db.ref("reservation").push(this.newUser);
    },
    createEmployee() {
      console.log("Click");
      db.ref("users").push(this.newUser);
      this.readEmployees();
    },
    readEmployees() {
      var leadsRef = db.ref('users');
leadsRef.on('value', function(snapshot) {
    snapshot.forEach(function(childSnapshot) {
          console.log("Data: ", childSnapshot.val());
    });
});
    },
    getResto() {
      var url = this.$adresse + this.$route.params.id;
      fetch(url, {
        headers: {
          Authorization: "Bearer " + this.$token,
        },
      })
        .then(function (response) {
          return response.json();
        })
        .then((text) => {
          console.log("Request successful", text);
          this.item = text;
          this.loading = false;
        })
        .catch(function (error) {
          console.log("Request failed", error);
        });
    },
    searchTimeOut() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.timer = setTimeout(() => {
        this.getResto();
      }, 1000);
    },
  },
};
</script>